import React from "react";
import "@/styles/ProjectContent.scss";
import { Grid2 } from "@mui/material";

//react-icons
import { TbNewSection } from "react-icons/tb";
import { MdEditNote } from "react-icons/md";
import ProjectCard from "@/components/ProjectCard";

const cardData = [
  { id: 1, title: "Card 1", description: "Description 1", image: "image1.jpg", alt: "image 1" },
  { id: 2, title: "Card 2", description: "Description 2", image: "image2.jpg", alt: "image 2" },
  { id: 3, title: "Card 3", description: "Description 3", image: "image3.jpg", alt: "image 3" }
];

function ProjectContent({handleCard}) {

  function handleCardClick(card) {
    handleCard(card);
  };
  return (
    <>
      <section>
        <div className="TitleProjectSection">
          <h1>Proyectos</h1>
          <h4>Crea y planifica tus proyectos</h4>
          <hr />
        </div>
      </section>
      <section>
        <div className="MenuProjectSection">
          {/* <TbNewSection /> */}
          <Grid2 container spacing={2} justifyContent="flex-start" alignItems="center" sx={{ padding: 2 }}>
      {cardData.map((card) => (
        <Grid2 xs={12} sm={6} md={6} key={card.id} onClick={() => handleCardClick(card.id)}>
          <ProjectCard
            title={card.title}
            description={card.description}
            image={card.image}
            alt={card.alt}
          />
        </Grid2>
      ))}
    </Grid2>
        </div>
        {/* {children} */}
      </section>
    </>
  );
}

export default ProjectContent;
